<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>

    <button class="btn btn-primary">CLICK</button>
    <div class="container" style="margin-top: 3%; margin-left: 2%;">
      <h1>Welcome to Face Overlay App</h1>
      <div class="col-md-6">
        <div></div>

        <hr />

        <form enctype="multipart/form-data" @submit.prevent="onSubmit">
          <div class="form-group">
            <select class="form-control" name="item" v-model="model.item">
              <option disabled value="">Choose an item</option>
              <option value="glasses"> Glasses </option>
              <option value="harlequinmask"> Harlequin Mask </option>
              <option value="real_facemask"> Face Mask </option>
            </select>
          </div>
          <div class="form-group">
            <label for="">File:</label>
            <input
              type="file"
              class="form-control"
              accept="image/*"
              name="image"
              v-on:change="upload($event.target.files)"
            />
          </div>
          <div class="form-group">
            <button class="btn btn-primary">Upload</button>
            {{ loading }}
          </div>
        </form>
      </div>

      <div class="col-md-4">
        <div class="col-md-6" style="margin-top: 20%;">
          <img
            id="originalface"
            class="img-responsive"
            alt=""
            width="600"
            height="600"
          />
        </div>
        <div class="col-md-6" style="margin-top: 20%;">
          <img
            :src="maskedface"
            class="img-responsive"
            alt=""
            width="400"
            height="400"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App",
      model: {
        text: "",
        image: null,
        item: ""
      },
      maskedface: null,
      loading: ""
    };
  },
  methods: {
    upload: function(files) {
      this.model.image = files[0];
      this.showPreview(files[0]);
    },
    showPreview: function(file) {
      var reader = new FileReader();
      reader.onload = function(e) {
        document.getElementById("originalface").src = e.target.result;
      };
      // read the image file as a data URL.
      reader.readAsDataURL(file);
    },
    onSubmit: function() {
      // Assemble form data
      const formData = new FormData();
      formData.append("image", this.model.image);
      formData.append("item", this.model.item);
      this.loading = "Processing....Please be patient.";

      // Post to server
      axios.post("http://localhost:3333/upload", formData).then(res => {
        // Update UI
        this.maskedface = res.data.url;
        this.loading = "";
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



