<style scoped></style>

<template>
  <div>
    <header>
      <div class="container">
        <section class="grid intro">
          <div class="column-xs-12 column-md-5">
            <h1>Buying Facemasks Just Got Cooler</h1>
            <p class="lead">
              Select a face mask and see how cool you look before your make a
              purchase
            </p>
            <router-link class="btn fill" to="/product">Shop now</router-link>
          </div>
        </section>
      </div>
    </header>
    <main>
      <div class="container">
        <section id="blog" class="grid">
          <div class="column-xs-12 text-center">
            <h2>New Arrivals</h2>
          </div>

          <div class="column-xs-12 column-md-3" v-for="mask in list" :key="mask.id">
            <router-link :to="'/product/' + mask.id">
              <figure>
                <div class="img-container">
                  <!-- <img :src="mask.image_url" /> -->
                  <img v-if="isLoading" src="../assets/loading.gif" style="width:100%;height:100%;" />
                  <img v-else :src="mask.image_url" />
                </div>
                <figcaption>
                  <h3>{{ mask.name }}</h3>
                  <p class="price">₦ {{ mask.price }}</p>
                </figcaption>
              </figure>
            </router-link>
          </div>

          <infinite-loading spinner="waveDots" @infinite="infiniteHandler">
            <span slot="no-more"></span>
          </infinite-loading>

          <!-- <div class="column-xs-12 text-center">
            <a class="btn outline" href="#">Shop All</a>
          </div>-->
        </section>
      </div>
    </main>
  </div>
</template>

<script>
import axios from "axios";
import InfiniteLoading from "vue-infinite-loading";
export default {
  components: {
    InfiniteLoading
  },
  data() {
    return {
      masks: {},
      page: 1,
      list: [],
      isLoading: true
    };
  },
  methods: {
    // infiniteHandler($state) {
    //   axios
    //     .get("http://facemask.test/api/getmasks/nigeria?page=", {
    //       params: {
    //         page: this.page
    //       }
    //     })
    //     .then(({ data }) => {
    //       if (data.data.length) {
    //         this.page += 1;
    //         this.masks.push(...data.data);
    //         $state.loaded();
    //       } else {
    //         $state.complete();
    //       }
    //     });
    //   // axios.get("http://facemask.test/api/getmasks/nigeria").then(res => {
    //   //   // console.log(res.data);
    //   //   this.masks = res.data.data;
    //   // });
    // },
    infiniteHandler($state) {
      axios
        .get("https://api.slydx.com/api/getmasks/nigeria?page=", {
          params: {
            page: this.page
          }
        })
        .then(({ data }) => {
          if (data.data.length) {
            this.page += 1;
            this.list.push(...data.data);
            this.isLoading = false;
            // console.log(this.list);
            $state.loaded();
          } else {
            $state.complete();
          }
        });
    }
  },
  mounted() {}
};
</script>



