        <!--Gallery Hero-->
        <div class="gallery__hero">
          <!-- <a href="" class="gallery__hero-enlarge ir" data-gallery="zoom"
            >Zoom</a
          >-->
          <img v-if="isLoading" src="../assets/loading.gif" style="width:100%;height:100%;" />
          <img v-else :src="product.image_url" />

          <div
            style="border-radius: 2px;height:40px;position:absolute;width:120px;background-color:#ea4335;color:white;top:0;right:0;padding-left:7px;padding-top:7px;padding-bottom:2px;font-size:14px;"
          >
            <p style="font-size: 15px">
              Price:
              <b>₦{{ product.price }}</b>
            </p>
          </div>
        </div>
        <!--Gallery Hero-->

        <!--Gallery Thumbs-->

        <div class="gallery__thumbs" style="padding-bottom: 10px;padding-top: 10px;">
          <div v-if="isLoading" class="text-center"></div>
          <h2 v-else class="text-center" style="font-size: 1.6rem;">{{product.name}}</h2>
          <div style="overflow: hidden;padding: 8px;padding-bottom: 0px;">
            <p style="float: left;">
              <i class="fas fa-list"></i> :
            </p>
            <div v-if="isLoading">
              <img src="../assets/loading.gif" style="width:20px;height:20px;float: right;" />
            </div>
            <p v-else style="float: right;">{{ product.category | capitalize }}</p>
          </div>
          <div style="overflow: hidden;padding-left: 8px;padding-right: 8px;">
            <p style="float: left;">
              <i class="fas fa-globe-africa"></i> :
            </p>
            <div v-if="isLoading">
              <img src="../assets/loading.gif" style="width:20px;height:20px;float: right;" />
            </div>
            <p v-else style="float: right;">{{ product.country | capitalize }}</p>
          </div>
          <div style="overflow: hidden;padding-left: 8px;padding-right: 8px;">
            <p style="float: left;">
              <i class="fas fa-truck"></i> :
            </p>
            <div v-if="isLoading">
              <img src="../assets/loading.gif" style="width:20px;height:20px;float: right;" />
            </div>
            <p v-if="product.delivery == 1 && !isLoading" style="float: right;">1-3 days</p>
            <p v-else-if="product.delivery == 2 && !isLoading" style="float: right;">4-7 days</p>
            <p v-else-if="product.delivery >= 3 && !isLoading" style="float: right;">> 7 days</p>
          </div>
          <div style="padding:10px">
            <button
              id="myBtn"
              class="btn"
              style="left: 0%;display:block;width: 100%;margin-bottom: 10px;color:white;"
            >View In AI</button>
            <a
              :href="'https://wa.me/' + product.vendor_number + '?text=I%20would%20like%20to%20purchase%20your%20facemask%20product%20from%20this%20website%20link%20' + getCurrentUrl()"
              class="btn"
              target="_blank"
              style="left: 0%; display:block;width: 100%;color:white;background:#075e54;padding: 1.125rem 3rem;"
            >Buy Via WhatsApp</a>
            <!-- <div v-if="isLoading">
              <img src="../assets/loading.gif" style="width:20px;height:20px;" />
            </div>-->

            <p
              v-if="!isLoading"
              style="margin-top:15px; margin-bottom:0px"
            >Sold By: {{ product.sold_by | capitalize }}</p>

            <!-- The Modal -->
            <div id="myModal" class="modal">
              <!-- Modal content -->
              <div class="modal-content">
                <span class="close">&times;</span>
                <div class="form-group">
                  <input
                    type="file"
                    accept="image/*"
                    name="image"
                    v-on:change="onUpload($event.target.files)"
                    id="file-input"
                  />
                  <div>{{ loading }}</div>
                  <img v-if="isLoading" src="../assets/loading.gif" style="width:100%;height:100%;" />
                  <cld-image
                    v-else
                    :publicId="avatar"
                    id="the_mask_man"
                    width="400"
                    height="400"
                    gravity="face"
                    crop="thumb"
                  >
                    <cld-transformation
                      width="0.9"
                      gravity="face"
                      flags="region_relative"
                      overlay="work_mask"
                    />
                  </cld-image>
                  <!-- <label>Upload a Selfie, Say Cheese</label>
                  <div class="col-sm-12" style="padding-left: 0px !important;">
                    <input
                      type="file"
                      accept="image/*"
                      name="image"
                      v-on:change="onSubmit($event.target.files)"
                      id="file-input"
