                    <div class="form-group">{{ loading }}</div>
                    <div class="col-md-6" style="margin-top: 20px;">
                      <img
                        :src="maskedface"
                        class="img-responsive"
                        alt
                        width="400"
                        height="400"
                      />  </div>
                  </div>-->
                  <a
                    :href="'https://wa.me/' + product.vendor_number + '?text=I%20would%20like%20to%20purchase%20your%20facemask%20product%20from%20this%20website%20link%20' + getCurrentUrl()"
                    class="btn"
                    target="_blank"
                    style="margin-top: 10px;left: 0%; display:block;width: 100%;color:white;background:#075e54;padding: 1.125rem 3rem;"
                  >Buy Via WhatsApp</a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!--Gallery Thumbs-->
      </div>
      <!--.gallery-->
      <!-- Gallery -->
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      model: {
        text: "",
        image: null,
        item: ""
      },
      maskedface: null,
      loading: "",
      product: {},
      avatar: "",
      cloudName: "obavigo",
      preset: "obavigo",
      isLoading: true
    };
  },
  methods: {
    getCurrentUrl() {
      return window.location.href;
    },
    upload: function(files) {
      this.model.image = files[0];
      this.showPreview(files[0]);
    },
    showPreview: function(file) {
      var reader = new FileReader();
      reader.onload = function(e) {
        // document.getElementById("originalface").src = e.target.result;
      };
      // read the image file as a data URL.
      reader.readAsDataURL(file);
    },
    onSubmit: function(files) {
      this.model.image = files[0];
      // Assemble form data
      const formData = new FormData();
      formData.append("image", this.model.image);
      formData.append("item", this.model.item);
      // console.log(formData);
      // this.loading = "Processing....Please be patient.";
      document.getElementById("the_mask_man").style.display = "none";

      // Post to server
      axios.post("http://localhost:3333/upload", formData).then(res => {
        // Update UI
        this.maskedface = res.data.url;
        document.getElementById("the_mask_man").style.display = "block";
        this.loading = "";
      });
    },
    onUpload: function(files) {
      // this.loading = "Processing....Please be patient.";
      // document.getElementById("the_mask_man").style.display = "none";
      this.isLoading = true;
      // this.model.image = files[0];
      // cloudinary.uploader.upload(this.model.image, function(error, result) {
      //   console.log(result);
      // });
      let reader = new FileReader();
      this.fileContents = reader.result;
      this.model.image = files[0];
      // Assemble form data
      const formData = new FormData();
      formData.append("image", this.model.image);
      formData.append("item", this.model.item);
      formData.append("upload_preset", this.preset);
      formData.append("file", this.model.image);
      // console.log(formData);
      // Post to server
      axios
        .post(
          `https://api.cloudinary.com/v1_1/${this.cloudName}/upload`,
          formData
        )
        .then(res => {
          // Update UI
          // console.log(res.data.secure_url);
          this.maskedface = res.data.secure_url;
          this.isLoading = false;
          // document.getElementById("the_mask_man").style.display = "block";
          this.loading = "";
          this.avatar = res.data.public_id;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },

  mounted() {
    let mask_id = this.$route.params.id;
    axios.get(`https://api.slydx.com/api/product/${mask_id}`).then(res => {
      this.product = res.data;
      this.model.item = res.data.mask_name;
      this.isLoading = false;
      // console.log(this.model.item);
    });
    // Get the modal
    var modal = document.getElementById("myModal");

    // Get the button that opens the modal
    var btn = document.getElementById("myBtn");

    // Get the <span> element that closes the modal
    var span = document.getElementsByClassName("close")[0];

    // When the user clicks on the button, open the modal
    btn.onclick = function() {
      modal.style.display = "block";
    };

    // When the user clicks on <span> (x), close the modal
    span.onclick = function() {
      modal.style.display = "none";
    };

    // When the user clicks anywhere outside of the modal, close it
    window.onclick = function(event) {
      if (event.target == modal) {
        modal.style.display = "none";
      }
    };
  }
};
</script>



